<script lang="ts">
import WALLET_CONFIG from '@/constants/wallet_config'
import { ElLoading } from 'element-plus'
import {
    defineComponent,
    onMounted,
    ref,
    getCurrentInstance,
    nextTick,
    Ref,
} from 'vue'
import TOOL from '../utils/tool'
import * as API from '@/constants/api'
import axios from 'axios'
import LogUtils from '@/utils/LogUtils'
import { number } from '@intlify/core-base'
import { Checkbox, showToast } from 'vant'
import { useRouter } from 'vue-router'
import tool from '../utils/tool'

export default defineComponent({
    name: 'Index',
    components: {
        Checkbox
    },
    setup() {
        const BUS = getCurrentInstance()?.appContext.config.globalProperties.$bus
        const isMobile = ref(false)
        let loadingInstance: any

        const router = useRouter()

        onMounted(() => {
            isMobile.value = !TOOL.getDeviceScreenIsPC()
            BUS.$on('RESIZE_WINDOW', (para: any) => {
                isMobile.value = para.isMobile
            })
        })

        const loadData = async () => {
            loadingInstance = ElLoading.service({ fullscreen: false, background: 'transparent', svg: WALLET_CONFIG.svgLoading, svgViewBox: WALLET_CONFIG.svgLoadingViewbox })

            nextTick(() => {
                // Loading should be closed asynchronously
                loadingInstance.close()
            })
        }

        return {
            isMobile,
        }
    }

})
</script>

