<template>
  <main ref="root" class="App d-flex flex-column" style="position:relative">
    <router-view />
  </main>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref, getCurrentInstance, provide, nextTick } from 'vue'
import { useRouter } from 'vue-router'
// import { useStore } from '../store'
import TOOL from '../utils/tool'
import useNotifications from '@/components/thirdparty/vue3-toast/notifications'
import ToastNotification from '@/components/thirdparty/vue3-toast/ToastNotification.vue'
import LogUtils from '@/utils/LogUtils';

export default defineComponent({
  name: 'Main',
  components: {
    ToastNotification,
  },

  setup() {
    const pageChainConfig: any = {}
    const root = ref(null)
    const isLoading = ref(true)
    const isShowMask = ref(false)
    const maskIndex = ref(-1)
    const modelObj = ref(null)

    const {
      notifications,
      createNotification,
      removeNotifications,
      stopBodyOverflow,
      allowBodyOverflow
    } = useNotifications()

    provide('create-notification', createNotification)


    // 父组件 ref 引用
    const header = ref(null);
    const HEADER_PROVIDE = 'headerProvide'
    // 提供父组件 ref 引用
    provide(HEADER_PROVIDE, header);

    const router = useRouter()

    // const store = useStore()

    function goAnchor(id: string) {
      const anchorEle = document.querySelector(id)
      if (id === '#parent') {
        if (anchorEle) {
          anchorEle.scrollIntoView({
            behavior: 'smooth',
            block: 'start',
            inline: 'center'
          })
        }
      } else if (anchorEle) {
        anchorEle.scrollIntoView({
          behavior: 'smooth',
          block: 'start',
          inline: 'center'
        })
      }
    }

    const BUS = getCurrentInstance()?.appContext.config.globalProperties.$bus

    const state = ref({
      height: window.innerHeight,
      width: window.innerWidth,
      isMobile: false
    })

    const resize = () => {
      state.value.isMobile = !TOOL.getDeviceScreenIsPC()
      state.value.height = window.innerHeight
      state.value.width = window.innerWidth

      BUS.$emit('RESIZE_WINDOW', {
        height: state.value.height,
        width: state.value.width,
        isMobile: state.value.isMobile
      })
    }

    const closeMask = () => {
      // setTimeout(() => {
      maskIndex.value = -1
      isShowMask.value = false
      modelObj.value = null
      BUS.$emit('CLOSE_VIDEO_MASK', {
        close: true
      })
      // }, 1000)
    }

    onMounted(async () => {
      // setTimeout(() => {
      //   mainContainer.value!.scrollTop = 0
      // }, 2000)

      // document.body.scrollTop = document.documentElement.scrollTop = 0;

      isLoading.value = false

      BUS.$on('REMOVE_NOTIFICATION', (para: any) => {
        const { id } = para
        removeNotifications(id)
      })

      resize()
      window.addEventListener('resize', resize)

      BUS.$on('SHOW_MASK', (para: any) => {
        maskIndex.value = para.maskIndex
        isShowMask.value = para.isShowMask
        if (para.maskIndex === 1) {
          const { modelIndex, nftList } = para
          modelObj.value = nftList
        }
      })
    })

    return {
      root,
      goAnchor,
      state,
      isShowMask,
      maskIndex,
      closeMask,
      isLoading,
      header,
      notifications,
      createNotification,
      removeNotifications,
      stopBodyOverflow,
      allowBodyOverflow,
    }
  }
})
</script>

<style scoped lang="stylus">
  main{
    height: 100%
    margin-bottom: 0
    padding-bottom: 0
    overflow: hidden;
  }
</style>
