import WALLET_CONFIG from '@/constants/wallet_config';

const logProductDebugSwitch = false
const LogUtils = {
    debug(d: any) {
        if (WALLET_CONFIG.isDebug || logProductDebugSwitch) {
            console.log('Debug:', d)
        }
    },
    info(i: any) {
        console.info(i)
    },
    warn(w: any) {
        console.warn(w)
    },
    error(e: any) {
        console.error('Error:', e)
    }
}

export default LogUtils