import { createI18n } from "vue-i18n"
import messages from "./locales";
// import en from "./root/en.json";
// import zh_tw from "./root/zh-TW.json";
// import ja from "./root/ja.json";


// 默认语言 - 
const langBrowser = navigator.language
const langBrowserDefault = (langBrowser === 'ja' || langBrowser === 'zh-TW') ? langBrowser : "en"
const domain = window.location.host
const parts = domain.split('.')

function getDomainLocale() {
    if (parts[0].toLowerCase() === 'tw') {
        return 'zh-TW'
    } else if (parts[0].toLowerCase() === 'ja') {
        return 'ja'
    } else {
        return null
    }
}

let default_lang = localStorage.getItem('locale') || getDomainLocale() || langBrowserDefault

const i18n = createI18n({
    legacy: false,
    locale: default_lang,
    fallbackLocale: "en",
    globalInjection: true,
    messages: {
        "en":
            messages.en
        ,
        "ja":
            messages.ja
        ,
        "zh-TW":
            messages["zh-TW"]

    }

})

export default i18n 